* {
  box-sizing: border-box;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  letter-spacing: .03em;
  background-color: #dadbe6;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 20px;
  line-height: 1.7;
}

h1, h2, h3, h4, h5, p, ul {
  margin: 0;
  padding: 0;
}

.no-scroll {
  overflow: hidden;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.search-form {
  font: inherit;
  width: 100%;
  height: 40px;
  background-color: #230780;
  border: 1px solid gray;
  margin-bottom: 16px;
}

.search-form__container {
  justify-content: center;
  align-items: center;
  margin: 10px 50%;
  display: flex;
}

.search-form__btn {
  cursor: pointer;
  background-color: #cac9c9;
  border: 1px solid #dadada;
  border-radius: 2px;
}

.gallery {
  grid-gap: 20px;
  flex-wrap: wrap;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  max-width: 500px;
  background-color: #fff;
  border: 1px solid #000;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.gallery__image {
  width: 100%;
  height: 80%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  display: block;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  text-align: center;
  flex-grow: 1;
  justify-content: space-around;
  padding-top: 15px;
  display: flex;
  bottom: 0;
}

.info-item {
  flex-direction: column;
  display: flex;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden, .button .spinner.is-hidden {
  display: none;
}

.btn-primary {
  color: #fff;
  text-align: center;
  letter-spacing: .5px;
  cursor: pointer;
  height: 36px;
  text-transform: uppercase;
  background-color: #227;
  border: none;
  border-radius: 2px;
  margin: 20px auto 40px;
  padding: 0 2rem;
  line-height: 36px;
  text-decoration: none;
  display: block;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .14), 0 1px 5px rgba(0, 0, 0, .12), 0 3px 1px -2px rgba(0, 0, 0, .2);
}

.btn-primary:hover {
  background-color: #5660e7;
}

